/* oswald-200 - latin-ext_latin */
@font-face {
     font-family: 'Oswald';
     font-style: normal;
     font-weight: 200;
     src: local('oswald-v47-latin-ext_latin-200'),
          url('./oswald-v47-latin-ext_latin-200.woff2') format('woff2'),
          /* Chrome 26+, Opera 23+, Firefox 39+ */
          url('./oswald-v47-latin-ext_latin-200.woff') format('woff');
     /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

